<script>
import axios from 'axios';

export default {    
    data() {
        return {
            selectedImage: null,
            apiKey: "ybobZiH8a2rXL5phZj8gyu0uEnM54AdXo8aoRtsScjg",
        };
    },
    async mounted() {
        const { data } = await axios.get(
            `https://api.unsplash.com/photos/${this.$route.params.id}?client_id=${this.apiKey}`,
        );
        this.selectedImage = data;
    },
    methods: {
        async likePhoto() {
            axios.post(
                `https://api.unsplash.com/photos/${this.$route.params.id}/like`,
                {},
                {
                    headers: {
                        Authorization: `Client-ID ${this.apiKey}`
                    }
                }
            )
            .then(response => {
                console.log("Photo liked successfully!");
            })
            .catch(error => {
                console.error("Error occurred while liking photo:", error);
            });
        }
    }
};
</script>

